import { useState } from "react";
import HeroSection from "@/components/hero-section";
import MovieGrid from "@/components/movie-grid";
import SearchBar from "@/components/search-bar";
import RandomPicker from "@/components/random-picker";
import MovieModal from "@/components/movie-modal";
import Navbar from '@/components/Navbar';

import { useTrendingMovies, usePopularTVShows, usePopularMovies } from "@/hooks/use-tmdb";
import type { TMDBMovie, TMDBTVShow } from "@/types/tmdb";

export default function Home() {
  const [selectedContent, setSelectedContent] = useState<(TMDBMovie | TMDBTVShow) | null>(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [searchQuery, setSearchQuery] = useState("");
  const [isSearchActive, setIsSearchActive] = useState(false);

  const { data: trendingMovies, isLoading: loadingTrending } = useTrendingMovies();
  const { data: popularMovies, isLoading: loadingPopular } = usePopularMovies();
  const { data: popularTVShows, isLoading: loadingTV } = usePopularTVShows();

  const handleContentSelect = (content: TMDBMovie | TMDBTVShow) => {
    // Navigate to details page instead of opening modal
    const isTV = 'name' in content;
    const path = isTV ? `/tv/${content.id}` : `/movie/${content.id}`;
    window.location.href = path;
  };

  const handlePlay = (tmdbId: number, isTV: boolean, seasonNumber?: number, episodeId?: number) => {
    let url;
    if (isTV && seasonNumber && episodeId) {
      url = `https://pstream.org/media/tmdb-tv-${tmdbId}/${seasonNumber}/${episodeId}`;
    } else if (isTV) {
      // Default to season 1, episode 1 for TV shows
      url = `https://pstream.org/media/tmdb-tv-${tmdbId}/1/1`;
    } else {
      url = `https://pstream.org/media/tmdb-movie-${tmdbId}`;
    }
    
    // Open stream in new tab
    window.open(url, '_blank');
  };

  const handleSearch = (query: string) => {
    setSearchQuery(query);
    setIsSearchActive(query.length > 0);
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
    setSelectedContent(null);
  };

  return (
    <div className="min-h-screen bg-netflix-dark">
      <Navbar />
      {/* Hero Section */}
      {!isSearchActive && (
        <HeroSection 
          featuredMovie={trendingMovies?.results?.[0]} 
          onPlay={handlePlay}
          onMoreInfo={handleContentSelect}
        />
      )}
      {/* Main Content */}
      <main className={`container mx-auto px-4 space-y-12 ${isSearchActive ? 'pt-24' : 'py-8'}`}>
        {/* Random Picker */}
        {!isSearchActive && (
          <RandomPicker onContentSelect={handleContentSelect} />
        )}
        {/* Content Grids */}
        {!isSearchActive ? (
          <>
            <MovieGrid
              title="Trending Movies"
              movies={trendingMovies?.results || []}
              isLoading={loadingTrending}
              onMovieSelect={handleContentSelect}
            />
            <MovieGrid
              title="Popular Movies"
              movies={popularMovies?.results || []}
              isLoading={loadingPopular}
              onMovieSelect={handleContentSelect}
            />
            <MovieGrid
              title="Popular TV Shows"
              movies={popularTVShows?.results || []}
              isLoading={loadingTV}
              onMovieSelect={handleContentSelect}
              isTV={true}
            />
          </>
        ) : (
          <MovieGrid
            title="Search Results"
            searchQuery={searchQuery}
            onMovieSelect={handleContentSelect}
          />
        )}
      </main>
      {/* Modals */}
      <MovieModal
        content={selectedContent}
        isOpen={isModalOpen}
        onClose={handleCloseModal}
        onPlay={handlePlay}
      />
    </div>
  );
}
