import Navbar from '@/components/Navbar';
import MovieGrid from '@/components/movie-grid';
import { usePopularTVShows, useSearch } from '@/hooks/use-tmdb';
import { useState } from 'react';

const TV_GENRES = [
  { id: 0, name: 'All' },
  { id: 10759, name: 'Action & Adventure' },
  { id: 16, name: 'Animation' },
  { id: 35, name: 'Comedy' },
  { id: 80, name: 'Crime' },
  { id: 99, name: 'Documentary' },
  { id: 18, name: 'Drama' },
  { id: 10751, name: 'Family' },
  { id: 10762, name: 'Kids' },
  { id: 9648, name: 'Mystery' },
  { id: 10763, name: 'News' },
  { id: 10764, name: 'Reality' },
  { id: 10765, name: 'Sci-Fi & Fantasy' },
  { id: 10766, name: 'Soap' },
  { id: 10767, name: 'Talk' },
  { id: 10768, name: 'War & Politics' },
  { id: 37, name: 'Western' },
];

export default function TVShowsPage() {
  const [genre, setGenre] = useState(0);
  const [search, setSearch] = useState('');
  const [page, setPage] = useState(1);
  const { data, isLoading } = usePopularTVShows(page);
  const { data: searchResults, isLoading: searchLoading } = useSearch(search);

  // Filter by genre and search
  let shows = [];
  let loading = false;
  if (search.trim().length > 2) {
    shows = (searchResults?.tv || []);
    loading = searchLoading;
  } else {
    shows = data?.results || [];
    loading = isLoading;
    if (genre !== 0) {
      shows = shows.filter(show => show.genre_ids.includes(genre));
    }
  }
  if (search.trim().length > 2 && genre !== 0) {
    shows = shows.filter(show => show.genre_ids.includes(genre));
  }

  return (
    <div className="min-h-screen bg-netflix-dark text-white">
      <Navbar />
      <main className="container mx-auto px-4 space-y-12 py-8">
        {/* Genre Filter Bar */}
        <div className="flex flex-wrap gap-2 mb-6">
          {TV_GENRES.map(g => (
            <button
              key={g.id}
              className={`px-4 py-2 rounded-full text-sm font-semibold transition-colors ${genre === g.id ? 'bg-netflix-red text-white' : 'bg-netflix-light-gray text-white hover:bg-netflix-red/80'}`}
              onClick={() => setGenre(g.id)}
            >
              {g.name}
            </button>
          ))}
        </div>
        {/* Search Bar */}
        <div className="mb-6">
          <input
            type="text"
            placeholder="Search TV shows..."
            value={search}
            onChange={e => setSearch(e.target.value)}
            className="w-full md:w-96 px-4 py-2 rounded bg-netflix-gray border border-netflix-light-gray !text-black !placeholder-black"
          />
        </div>
        {/* TV Shows Grid */}
        <MovieGrid
          title={search.trim().length > 2 ? "Search Results" : "Popular TV Shows"}
          movies={shows}
          isLoading={loading}
          onMovieSelect={(show) => {
            window.location.href = `/tv/${show.id}`;
          }}
          isTV={true}
        />
        {/* Pagination */}
        {search.trim().length <= 2 && (
          <div className="flex justify-center gap-4 mt-8">
            <button
              className="px-4 py-2 rounded bg-netflix-light-gray text-white disabled:opacity-50"
              onClick={() => setPage(p => Math.max(1, p - 1))}
              disabled={page === 1}
            >
              Previous
            </button>
            <span className="px-4 py-2">Page {page}</span>
            <button
              className="px-4 py-2 rounded bg-netflix-light-gray text-white disabled:opacity-50"
              onClick={() => setPage(p => p + 1)}
              disabled={data && data.results.length < 20}
            >
              Next
            </button>
          </div>
        )}
      </main>
    </div>
  );
} 