import { useNavigate } from 'react-router-dom';

export default function Navbar() {
  const navigate = useNavigate();
  return (
    <header className="w-full z-50 bg-netflix-dark/95 backdrop-blur-sm transition-all duration-300">
      <nav className="container mx-auto px-4 py-4 flex items-center justify-between">
        <div className="flex items-center space-x-8">
          <h1 className="text-2xl font-bold text-netflix-red cursor-pointer" onClick={() => navigate('/')}>FMFAU</h1>
          <ul className="hidden md:flex space-x-6">
            <li>
              <button onClick={() => navigate('/')} className="hover:text-netflix-text transition-colors">Home</button>
            </li>
            <li>
              <button onClick={() => navigate('/movies')} className="hover:text-netflix-text transition-colors">Movies</button>
            </li>
            <li>
              <button onClick={() => navigate('/tv')} className="hover:text-netflix-text transition-colors">TV Shows</button>
            </li>
          </ul>
        </div>
      </nav>
    </header>
  );
} 