import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import { QueryClientProvider } from '@tanstack/react-query';
import { queryClient } from './lib/queryClient';
import { Toaster } from "@/components/ui/toaster";
import { TooltipProvider } from "@/components/ui/tooltip";
import Home from './pages/home';
import NotFound from "@/pages/not-found";
import MovieDetails from './pages/movie/[id]';
import TVShowDetails from './pages/tv/[id]';
import TVShowsPage from './pages/tv/index';
import MoviesPage from './pages/movie/index';

export default function AppRouter() {
  return (
    <QueryClientProvider client={queryClient}>
      <Router>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/movie/:id" element={<MovieDetails />} />
          <Route path="/movies" element={<MoviesPage />} />
          <Route path="/tv/:id" element={<TVShowDetails />} />
          <Route path="/tv" element={<TVShowsPage />} />
        </Routes>
      </Router>
    </QueryClientProvider>
  );
}

function App() {
  return (
    <QueryClientProvider client={queryClient}>
      <TooltipProvider>
        <div className="min-h-screen bg-netflix-dark text-white">
          <Toaster />
          <AppRouter />
        </div>
      </TooltipProvider>
    </QueryClientProvider>
  );
}
