import Navbar from '@/components/Navbar';
import MovieGrid from '@/components/movie-grid';
import { usePopularMovies } from '@/hooks/use-tmdb';
import { useState } from 'react';

const MOVIE_GENRES = [
  { id: 0, name: 'All' },
  { id: 28, name: 'Action' },
  { id: 12, name: 'Adventure' },
  { id: 16, name: 'Animation' },
  { id: 35, name: 'Comedy' },
  { id: 80, name: 'Crime' },
  { id: 99, name: 'Documentary' },
  { id: 18, name: 'Drama' },
  { id: 10751, name: 'Family' },
  { id: 14, name: 'Fantasy' },
  { id: 36, name: 'History' },
  { id: 27, name: 'Horror' },
  { id: 10402, name: 'Music' },
  { id: 9648, name: 'Mystery' },
  { id: 10749, name: 'Romance' },
  { id: 878, name: 'Science Fiction' },
  { id: 10770, name: 'TV Movie' },
  { id: 53, name: 'Thriller' },
  { id: 10752, name: 'War' },
  { id: 37, name: 'Western' },
];

export default function MoviesPage() {
  const [genre, setGenre] = useState(0);
  const [search, setSearch] = useState('');
  const [page, setPage] = useState(1);
  const { data, isLoading } = usePopularMovies(page);

  // Filter by genre and search
  let movies = data?.results || [];
  if (genre !== 0) {
    movies = movies.filter(movie => movie.genre_ids.includes(genre));
  }
  if (search.trim()) {
    movies = movies.filter(movie => movie.title.toLowerCase().includes(search.trim().toLowerCase()));
  }

  return (
    <div className="min-h-screen bg-netflix-dark text-white">
      <Navbar />
      <div className="container mx-auto px-4 py-8">
        {/* Genre Filter Bar */}
        <div className="flex flex-wrap gap-2 mb-6">
          {MOVIE_GENRES.map(g => (
            <button
              key={g.id}
              className={`px-4 py-2 rounded-full text-sm font-semibold transition-colors ${genre === g.id ? 'bg-netflix-red text-white' : 'bg-netflix-light-gray text-white hover:bg-netflix-red/80'}`}
              onClick={() => setGenre(g.id)}
            >
              {g.name}
            </button>
          ))}
        </div>
        {/* Search Bar */}
        <div className="mb-6">
          <input
            type="text"
            placeholder="Search movies..."
            value={search}
            onChange={e => setSearch(e.target.value)}
            className="w-full md:w-96 px-4 py-2 rounded bg-netflix-gray border border-netflix-light-gray !text-black !placeholder-black"
          />
        </div>
        {/* Movies Grid */}
        <MovieGrid
          title="Popular Movies"
          movies={movies}
          isLoading={isLoading}
          onMovieSelect={(movie) => {
            window.location.href = `/movie/${movie.id}`;
          }}
        />
        {/* Pagination */}
        <div className="flex justify-center gap-4 mt-8">
          <button
            className="px-4 py-2 rounded bg-netflix-light-gray text-white disabled:opacity-50"
            onClick={() => setPage(p => Math.max(1, p - 1))}
            disabled={page === 1}
          >
            Previous
          </button>
          <span className="px-4 py-2">Page {page}</span>
          <button
            className="px-4 py-2 rounded bg-netflix-light-gray text-white disabled:opacity-50"
            onClick={() => setPage(p => p + 1)}
            disabled={data && data.results.length < 20}
          >
            Next
          </button>
        </div>
      </div>
    </div>
  );
} 