import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import type { TMDBMovie, TMDBTVShow, TMDBResponse, SearchResults, TMDBSeason } from "@/types/tmdb";
import { apiRequest } from "@/lib/queryClient";

export function useTrendingMovies() {
  return useQuery<TMDBResponse<TMDBMovie>>({
    queryKey: ["/api/movies/trending"],
  });
}

export function usePopularMovies(page = 1) {
  return useQuery<TMDBResponse<TMDBMovie>>({
    queryKey: ["/api/movies/popular", page],
  });
}

export function useTopRatedMovies(page = 1) {
  return useQuery<TMDBResponse<TMDBMovie>>({
    queryKey: ["/api/movies/top-rated", page],
  });
}

export function useNowPlayingMovies(page = 1) {
  return useQuery<TMDBResponse<TMDBMovie>>({
    queryKey: ["/api/movies/now-playing", page],
  });
}

export function useMovieDetails(id: number, options?: { enabled?: boolean }) {
  return useQuery<TMDBMovie>({
    queryKey: ["/api/movies", id],
    queryFn: () => fetch(`/api/movies/${id}`).then(res => res.json()),
    enabled: options?.enabled !== undefined ? options.enabled : !!id,
  });
}

export function useTrendingTVShows() {
  return useQuery<TMDBResponse<TMDBTVShow>>({
    queryKey: ["/api/tv/trending"],
  });
}

export function usePopularTVShows(page = 1) {
  return useQuery<TMDBResponse<TMDBTVShow>>({
    queryKey: ["/api/tv/popular", page],
  });
}

export function useTVShowDetails(id: number, options?: { enabled?: boolean }) {
  return useQuery<TMDBTVShow>({
    queryKey: ["/api/tv", id],
    queryFn: () => fetch(`/api/tv/${id}`).then(res => res.json()),
    enabled: options?.enabled !== undefined ? options.enabled : !!id,
  });
}

export function useTVSeason(showId: number, seasonNumber: number, options?: { enabled?: boolean }) {
  return useQuery<TMDBSeason>({
    queryKey: ["/api/tv", showId, "season", seasonNumber],
    queryFn: () => fetch(`/api/tv/${showId}/season/${seasonNumber}`).then(res => res.json()),
    enabled: options?.enabled !== undefined ? options.enabled : (!!showId && !!seasonNumber),
  });
}

export function useSearch(query: string) {
  return useQuery<SearchResults>({
    queryKey: ["/api/search", query],
    enabled: query.length > 2,
  });
}

export function useFavorites() {
  return useQuery({
    queryKey: ["/api/favorites"],
  });
}

export function useAddToFavorites() {
  const queryClient = useQueryClient();
  
  return useMutation({
    mutationFn: async (data: { tmdbId: number; mediaType: string; title: string; posterPath?: string }) => {
      const response = await apiRequest("POST", "/api/favorites", data);
      return response.json();
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["/api/favorites"] });
    },
  });
}

export function useRemoveFromFavorites() {
  const queryClient = useQueryClient();
  
  return useMutation({
    mutationFn: async ({ tmdbId, mediaType }: { tmdbId: number; mediaType: string }) => {
      const response = await apiRequest("DELETE", `/api/favorites/${tmdbId}/${mediaType}`);
      return response.json();
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["/api/favorites"] });
    },
  });
}

export function useWatchHistory() {
  return useQuery({
    queryKey: ["/api/watch-history"],
  });
}

export function useAddToWatchHistory() {
  const queryClient = useQueryClient();
  
  return useMutation({
    mutationFn: async (data: { tmdbId: number; mediaType: string; title: string; progress?: number }) => {
      const response = await apiRequest("POST", "/api/watch-history", data);
      return response.json();
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["/api/watch-history"] });
    },
  });
}
