import { Button } from "@/components/ui/button";
import { Play, Info } from "lucide-react";
import type { TMDBMovie } from "@/types/tmdb";

interface HeroSectionProps {
  featuredMovie?: TMDBMovie;
  onPlay: (tmdbId: number, isTV: boolean) => void;
  onMoreInfo: (movie: TMDBMovie) => void;
}

export default function HeroSection({ featuredMovie, onPlay, onMoreInfo }: HeroSectionProps) {
  if (!featuredMovie) {
    return (
      <section className="relative h-screen flex items-center justify-start pt-20">
        <div className="absolute inset-0 bg-gradient-to-r from-netflix-dark via-netflix-dark/70 to-transparent z-10"></div>
        <div 
          className="absolute inset-0 bg-cover bg-center"
          style={{
            backgroundImage: `url('https://images.unsplash.com/photo-1489599511985-e2f1b5a2bca4?ixlib=rb-4.0.3&auto=format&fit=crop&w=1920&h=1080')`
          }}
        ></div>
        
        <div className="relative z-20 container mx-auto px-4">
          <div className="max-w-2xl">
            <h2 className="text-5xl md:text-7xl font-bold mb-4">Welcome to FMFAU</h2>
            <p className="text-lg md:text-xl text-netflix-text mb-6 max-w-lg">
              Discover the latest blockbuster movies and binge-worthy TV series. Stream instantly with crystal clear quality.
            </p>
          </div>
        </div>
      </section>
    );
  }

  const backdropUrl = featuredMovie.backdrop_path 
    ? `https://image.tmdb.org/t/p/original${featuredMovie.backdrop_path}`
    : 'https://images.unsplash.com/photo-1489599511985-e2f1b5a2bca4?ixlib=rb-4.0.3&auto=format&fit=crop&w=1920&h=1080';

  return (
    <section className="relative h-screen flex items-center justify-start pt-20">
      <div className="absolute inset-0 hero-gradient z-10"></div>
      <div 
        className="absolute inset-0 bg-cover bg-center"
        style={{ backgroundImage: `url('${backdropUrl}')` }}
      ></div>
      
      <div className="relative z-20 container mx-auto px-4">
        <div className="max-w-2xl">
          <h2 className="text-5xl md:text-7xl font-bold mb-4">{featuredMovie.title}</h2>
          <p className="text-lg md:text-xl text-netflix-text mb-6 max-w-lg">
            {featuredMovie.overview}
          </p>
          
          <div className="flex items-center space-x-4 mb-6">
            <div className="flex items-center space-x-2">
              <i className="fas fa-star text-imdb-yellow"></i>
              <span className="text-lg font-semibold">{featuredMovie.vote_average.toFixed(1)}</span>
            </div>
            <span className="text-netflix-text">
              {new Date(featuredMovie.release_date).getFullYear()}
            </span>
            <span className="bg-netflix-red px-2 py-1 rounded text-sm">HD</span>
          </div>
          
          <div className="flex space-x-4">
            <Button 
              className="bg-white text-black hover:bg-gray-200 font-semibold py-3 px-8 rounded-lg"
              onClick={() => onPlay(featuredMovie.id, false)}
            >
              <Play className="mr-2 h-5 w-5" />
              Play
            </Button>
            <Button 
              variant="secondary"
              className="netflix-button-secondary"
              onClick={() => onMoreInfo(featuredMovie)}
            >
              <Info className="mr-2 h-5 w-5" />
              More Info
            </Button>
          </div>
        </div>
      </div>
    </section>
  );
}
