import Navbar from '@/components/Navbar';
import { useParams } from 'react-router-dom';
import { useMovieDetails } from '@/hooks/use-tmdb';
import { Star, Play, Film } from 'lucide-react';
import { Button } from '@/components/ui/button';

export default function MovieDetails() {
  const { id } = useParams<{ id: string }>();
  const { data: movie, isLoading } = useMovieDetails(Number(id));

  if (isLoading) {
    return <div>Loading...</div>;
  }

  if (!movie) {
    return <div>Movie not found</div>;
  }

  const posterUrl = movie.poster_path 
    ? `https://image.tmdb.org/t/p/w500${movie.poster_path}`
    : null;
  const backdropUrl = movie.backdrop_path
    ? `https://image.tmdb.org/t/p/original${movie.backdrop_path}`
    : posterUrl;
  const genres = movie.genre_ids?.length
    ? movie.genre_ids.map(id => MOVIE_GENRES[id]).filter(Boolean)
    : [];
  const year = movie.release_date ? new Date(movie.release_date).getFullYear() : 'N/A';
  const runtime = movie.runtime ? `${movie.runtime} min` : '';
  const director = movie.crew?.find(c => c.job === 'Director')?.name;
  const writers = movie.crew?.filter(c => c.job === 'Writer' || c.job === 'Screenplay').map(c => c.name).join(', ');
  const trailer = movie.videos?.find(video => video.type === 'Trailer' && video.site === 'YouTube');

  return (
    <div className="min-h-screen bg-netflix-dark text-white overflow-y-visible">
      <Navbar />
      {/* Blurred Backdrop */}
      <div className="relative w-full h-[28vh] md:h-[36vh] overflow-hidden">
        {backdropUrl && (
          <img
            src={backdropUrl}
            alt={movie.title}
            className="absolute inset-0 w-full h-full object-cover object-top blur-md scale-110"
            style={{ filter: 'blur(24px) brightness(0.5)' }}
          />
        )}
        <div className="absolute inset-0 bg-gradient-to-t from-black/90 via-black/60 to-transparent" />
      </div>
      {/* Overlay Card */}
      <div className="container mx-auto px-4 -mt-16 md:-mt-24 relative z-10">
        <div className="flex flex-col md:flex-row gap-8 items-end md:items-center bg-black/70 rounded-2xl shadow-2xl p-6 md:p-10 border border-netflix-light-gray">
          {/* Poster */}
          {posterUrl && (
            <img
              src={posterUrl}
              alt={movie.title}
              className="w-40 h-60 md:w-56 md:h-80 object-cover rounded-xl shadow-lg border border-netflix-light-gray bg-black/40"
            />
          )}
          {/* Info Section */}
          <div className="flex-1 flex flex-col gap-4">
            <div className="flex flex-col md:flex-row md:items-center md:space-x-6 gap-2">
              <h2 className="text-4xl md:text-5xl font-extrabold mb-1 md:mb-0 drop-shadow-lg">{movie.title}</h2>
              <span className="bg-netflix-red px-3 py-1 rounded-full text-sm font-bold shadow">Movie</span>
              <span className="text-netflix-text text-lg font-semibold">{year}</span>
              {runtime && <span className="text-netflix-text text-lg font-semibold">{runtime}</span>}
              <div className="flex items-center space-x-2">
                <Star className="h-6 w-6 text-imdb-yellow fill-current" />
                <span className="font-bold text-lg">{movie.vote_average?.toFixed(1)}</span>
              </div>
            </div>
            {/* Genre Chips */}
            <div className="flex flex-wrap gap-2">
              {genres.map(g => (
                <span key={g} className="bg-white/10 border border-white/20 px-3 py-1 rounded-full text-sm font-semibold text-white shadow-sm">{g}</span>
              ))}
            </div>
            {/* Buttons */}
            <div className="flex space-x-4 mt-2">
              <Button className="bg-netflix-red hover:bg-netflix-red/80 text-white font-bold px-6 py-3 rounded-lg text-lg shadow-lg" onClick={() => window.open(`https://pstream.org/media/tmdb-movie-${movie.id}`, '_blank')}>
                <Play className="mr-2 h-5 w-5" />
                Stream
              </Button>
              {trailer && (
                <Button variant="secondary" className="bg-blue-600 hover:bg-blue-700 text-white font-bold px-6 py-3 rounded-lg text-lg shadow-lg" onClick={() => window.open(`https://www.youtube.com/watch?v=${trailer.key}`, '_blank')}>
                  <Film className="mr-2 h-5 w-5" />
                  Trailer
                </Button>
              )}
            </div>
            {/* Overview */}
            <p className="text-netflix-text text-lg mb-2 max-w-2xl leading-relaxed font-medium drop-shadow">
              {movie.overview}
            </p>
            {/* Director/Writers */}
            <div className="mb-2">
              {director && <div><span className="font-semibold">Director: </span><span className="text-netflix-text">{director}</span></div>}
              {writers && <div><span className="font-semibold">Writers: </span><span className="text-netflix-text">{writers}</span></div>}
            </div>
            {/* Main Cast */}
            {movie.credits && movie.credits.length > 0 && (
              <div className="mt-4">
                <h3 className="text-xl font-bold mb-2">Main Cast</h3>
                <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                  {movie.credits.slice(0, 8).map((actor) => (
                    <div key={actor.id} className="text-center">
                      <div className="w-16 h-16 rounded-full mx-auto mb-2 overflow-hidden bg-netflix-light-gray border border-white/20">
                        {actor.profile_path ? (
                          <img
                            src={`https://image.tmdb.org/t/p/w200${actor.profile_path}`}
                            alt={actor.name}
                            className="w-full h-full object-cover"
                          />
                        ) : (
                          <div className="w-full h-full flex items-center justify-center text-netflix-text text-xs">
                            No Photo
                          </div>
                        )}
                      </div>
                      <p className="font-medium text-sm">{actor.name}</p>
                      <p className="text-xs text-netflix-text">{actor.character}</p>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

// Genre mapping for movie genres
const MOVIE_GENRES: Record<number, string> = {
  28: "Action",
  12: "Adventure",
  16: "Animation",
  35: "Comedy",
  80: "Crime",
  99: "Documentary",
  18: "Drama",
  10751: "Family",
  14: "Fantasy",
  36: "History",
  27: "Horror",
  10402: "Music",
  9648: "Mystery",
  10749: "Romance",
  878: "Science Fiction",
  10770: "TV Movie",
  53: "Thriller",
  10752: "War",
  37: "Western"
}; 