// @ts-ignore
import { useParams, useNavigate } from 'react-router-dom';
import { useTVShowDetails, useTVSeason } from '@/hooks/use-tmdb';
import { Star, Play, ArrowLeft, Film } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { useState, useRef, useEffect } from 'react';
import Navbar from '@/components/Navbar';

export default function TVShowDetails() {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const [season, setSeason] = useState(1);
  const { data: tvShow, isLoading: isLoadingTV } = useTVShowDetails(Number(id));
  const { data: seasonData, isLoading: isLoadingSeason } = useTVSeason(Number(id), season);
  const bannerRef = useRef<HTMLDivElement>(null);

  // Scroll to banner/episodes when season changes
  useEffect(() => {
    if (bannerRef.current) {
      bannerRef.current.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
  }, [season]);

  if (isLoadingTV || isLoadingSeason) {
    return <div>Loading...</div>;
  }

  if (!tvShow) {
    return <div>TV Show not found</div>;
  }

  const posterUrl = tvShow.poster_path 
    ? `https://image.tmdb.org/t/p/w500${tvShow.poster_path}`
    : null;

  const backdropUrl = tvShow.backdrop_path
    ? `https://image.tmdb.org/t/p/original${tvShow.backdrop_path}`
    : posterUrl;

  // Genres as comma-separated string
  const genres = tvShow.genre_ids && tvShow.genre_ids.length > 0
    ? tvShow.genre_ids.map(id => TV_GENRES[id]).filter(Boolean).join(', ')
    : '';
  const year = tvShow.first_air_date ? new Date(tvShow.first_air_date).getFullYear() : 'N/A';
  const runtime = '';
  const creators = '';
  const trailer = tvShow.videos?.find(video => video.type === 'Trailer' && video.site === 'YouTube');

  // Find the current season's poster and name
  const currentSeason = tvShow.seasons?.find(s => s.season_number === season);
  const seasonPoster = currentSeason?.poster_path ? `https://image.tmdb.org/t/p/w300${currentSeason.poster_path}` : null;
  const seasonName = currentSeason?.name || `Season ${season}`;
  const seasonYear = currentSeason?.air_date ? new Date(currentSeason.air_date).getFullYear() : '';

  return (
    <div className="min-h-screen bg-netflix-dark text-white overflow-y-visible">
      <Navbar />
      {/* Blurred Backdrop */}
      <div className="relative w-full h-[28vh] md:h-[36vh] overflow-hidden">
        {backdropUrl && (
          <img
            src={backdropUrl}
            alt={tvShow.name}
            className="absolute inset-0 w-full h-full object-cover object-top blur-md scale-110"
            style={{ filter: 'blur(24px) brightness(0.5)' }}
          />
        )}
        <div className="absolute inset-0 bg-gradient-to-t from-black/90 via-black/60 to-transparent" />
      </div>
      {/* Overlay Card */}
      <div className="container mx-auto px-4 -mt-16 md:-mt-24 relative z-10">
        <div className="flex flex-col md:flex-row gap-8 items-end md:items-center bg-black/70 rounded-2xl shadow-2xl p-6 md:p-10 border border-netflix-light-gray">
          {/* Poster */}
          {posterUrl && (
            <img
              src={posterUrl}
              alt={tvShow.name}
              className="w-40 h-60 md:w-56 md:h-80 object-cover rounded-xl shadow-lg border border-netflix-light-gray bg-black/40"
            />
          )}
          {/* Info Section */}
          <div className="flex-1">
            <div className="flex flex-col md:flex-row md:items-center md:space-x-6 gap-2">
              <h2 className="text-4xl md:text-5xl font-extrabold mb-1 md:mb-0 drop-shadow-lg">{tvShow.name}</h2>
              <span className="bg-netflix-red px-3 py-1 rounded-full text-sm font-bold shadow">TV Series</span>
              <span className="text-netflix-text text-lg font-semibold">{year}</span>
              <div className="flex items-center space-x-2">
                <Star className="h-6 w-6 text-imdb-yellow fill-current" />
                <span className="font-bold text-lg">{tvShow.vote_average?.toFixed(1)}</span>
              </div>
            </div>
            {/* Genre Chips */}
            <div className="flex flex-wrap gap-2">
              {tvShow.genre_ids && tvShow.genre_ids.map(id => (
                TV_GENRES[id] && <span key={id} className="bg-white/10 border border-white/20 px-3 py-1 rounded-full text-sm font-semibold text-white shadow-sm">{TV_GENRES[id]}</span>
              ))}
            </div>
            {/* Buttons */}
            <div className="flex space-x-4 mt-2">
              <Button className="bg-netflix-red hover:bg-netflix-red/80 text-white font-bold px-6 py-3 rounded-lg text-lg shadow-lg" onClick={() => window.open(`https://pstream.org/media/tmdb-tv-${tvShow.id}/1/1`, '_blank')}>
                <Play className="mr-2 h-5 w-5" />
                Stream
              </Button>
              {trailer && (
                <Button variant="secondary" className="bg-blue-600 hover:bg-blue-700 text-white font-bold px-6 py-3 rounded-lg text-lg shadow-lg" onClick={() => window.open(`https://www.youtube.com/watch?v=${trailer.key}`, '_blank')}>
                  <Film className="mr-2 h-5 w-5" />
                  Trailer
                </Button>
              )}
            </div>
            {/* Overview */}
            <p className="text-netflix-text text-lg mb-4 max-w-2xl leading-relaxed">{tvShow.overview}</p>
            {/* Season Selector as horizontal buttons */}
            <div className="flex flex-wrap gap-2 mb-8">
              {Array.from({ length: tvShow.number_of_seasons || 1 }, (_, i) => (
                <Button
                  key={i + 1}
                  className={`px-4 py-2 rounded-full ${season === i + 1 ? 'bg-netflix-red text-white' : 'bg-netflix-light-gray text-white hover:bg-netflix-red/80'}`}
                  onClick={() => setSeason(i + 1)}
                >
                  Season {i + 1}
                </Button>
              ))}
            </div>
            {/* Main Cast */}
            {tvShow.credits && tvShow.credits.length > 0 && (
              <div className="mt-4">
                <h3 className="text-xl font-bold mb-2">Main Cast</h3>
                <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                  {tvShow.credits.slice(0, 8).map((actor) => (
                    <div key={actor.id} className="text-center">
                      <div className="w-16 h-16 rounded-full mx-auto mb-2 overflow-hidden bg-netflix-light-gray border border-white/20">
                        {actor.profile_path ? (
                          <img
                            src={`https://image.tmdb.org/t/p/w200${actor.profile_path}`}
                            alt={actor.name}
                            className="w-full h-full object-cover"
                          />
                        ) : (
                          <div className="w-full h-full flex items-center justify-center text-netflix-text text-xs">
                            No Photo
                          </div>
                        )}
                      </div>
                      <p className="font-medium text-sm">{actor.name}</p>
                      <p className="text-xs text-netflix-text">{actor.character}</p>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
      {/* Season Banner */}
      <div ref={bannerRef} className="flex items-center gap-6 bg-netflix-gray rounded-lg p-6 mb-8 mt-8">
        {seasonPoster && (
          <img src={seasonPoster} alt={seasonName} className="w-24 h-36 object-cover rounded shadow" />
        )}
        <div>
          <h3 className="text-2xl font-bold">{seasonName} {seasonYear && <span className="text-netflix-text">({seasonYear})</span>}</h3>
          {currentSeason?.overview && <p className="text-netflix-text mt-2 max-w-xl">{currentSeason.overview}</p>}
        </div>
      </div>
      {/* Episodes Section */}
      {seasonData && seasonData.episodes && seasonData.episodes.length > 0 ? (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          {seasonData.episodes.map((episode) => (
            <div key={episode.id} className="flex bg-netflix-gray rounded-lg shadow-md overflow-hidden">
              {episode.still_path && (
                <img
                  src={`https://image.tmdb.org/t/p/w300${episode.still_path}`}
                  alt={episode.name}
                  className="w-40 h-32 object-cover"
                />
              )}
              <div className="flex-1 p-4 flex flex-col justify-between">
                <div>
                  <h4 className="font-semibold text-lg mb-1">{episode.episode_number}. {episode.name}</h4>
                  <div className="flex items-center space-x-3 text-sm mb-1">
                    <span className="bg-netflix-red px-2 py-0.5 rounded text-xs font-semibold">{episode.vote_average ? `${episode.vote_average.toFixed(1)}` : 'N/A'}</span>
                    <span className="text-netflix-text">{episode.air_date}</span>
                    <span className="text-netflix-text">{episode.runtime} min</span>
                  </div>
                  <p className="text-netflix-text mb-2 line-clamp-3">{episode.overview}</p>
                </div>
                <Button className="netflix-button w-fit mt-2" onClick={() => window.open(`https://pstream.org/media/tmdb-tv-${tvShow.id}/${currentSeason?.id}/${episode.id}`, '_blank')}>
                  <Play className="mr-2 h-4 w-4" />
                  Stream
                </Button>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <div className="text-netflix-text text-lg mt-8">No episodes found for this season.</div>
      )}
    </div>
  );
}

// Genre mapping for TV genres
const TV_GENRES: Record<number, string> = {
  10759: "Action & Adventure",
  16: "Animation",
  35: "Comedy",
  80: "Crime",
  99: "Documentary",
  18: "Drama",
  10751: "Family",
  10762: "Kids",
  9648: "Mystery",
  10763: "News",
  10764: "Reality",
  10765: "Sci-Fi & Fantasy",
  10766: "Soap",
  10767: "Talk",
  10768: "War & Politics",
  37: "Western"
}; 