import { Star } from "lucide-react";
import { Skeleton } from "@/components/ui/skeleton";
import { useSearch } from "@/hooks/use-tmdb";
import type { TMDBMovie, TMDBTVShow } from "@/types/tmdb";
import { Button } from "@/components/ui/button";

interface MovieGridProps {
  title: string;
  movies?: (TMDBMovie | TMDBTVShow)[];
  isLoading?: boolean;
  onMovieSelect: (movie: TMDBMovie | TMDBTVShow) => void;
  isTV?: boolean;
  searchQuery?: string;
}

export default function MovieGrid({ 
  title, 
  movies = [], 
  isLoading = false, 
  onMovieSelect, 
  isTV = false,
  searchQuery 
}: MovieGridProps) {
  const { data: searchResults, isLoading: searchLoading } = useSearch(searchQuery || "");

  const displayMovies = searchQuery ? [
    ...(searchResults?.movies || []),
    ...(searchResults?.tv || [])
  ] : movies;

  const displayLoading = searchQuery ? searchLoading : isLoading;

  if (displayLoading) {
    return (
      <section className="space-y-6">
        <h3 className="text-2xl font-bold">{title}</h3>
        <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-6 gap-4">
          {Array.from({ length: 12 }).map((_, i) => (
            <div key={i} className="space-y-2">
              <Skeleton className="w-full h-64 rounded-lg bg-netflix-gray" />
              <Skeleton className="h-4 w-3/4 bg-netflix-gray" />
              <Skeleton className="h-3 w-1/2 bg-netflix-gray" />
            </div>
          ))}
        </div>
      </section>
    );
  }

  if (displayMovies.length === 0) {
    return (
      <section className="space-y-6">
        <h3 className="text-2xl font-bold">{title}</h3>
        <div className="text-center py-12">
          <p className="text-netflix-text text-lg">
            {searchQuery ? "No results found. Try a different search term." : "No content available."}
          </p>
        </div>
      </section>
    );
  }

  return (
    <section className="space-y-6">
      <h3 className="text-2xl font-bold">{title}</h3>
      <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-6 gap-4">
        {displayMovies.map((item) => {
          const posterUrl = item.poster_path 
            ? `https://image.tmdb.org/t/p/w300${item.poster_path}`
            : null;
          
          const itemTitle = 'title' in item ? item.title : item.name;
          const rating = item.vote_average?.toFixed(1) || 'N/A';
          const itemIsTV = 'name' in item;

          return (
            <div
              key={`${item.id}-${itemIsTV ? 'tv' : 'movie'}`}
              className="movie-card cursor-pointer group"
            >
              <div 
                className="relative overflow-hidden rounded-lg"
                onClick={() => onMovieSelect(item)}
              >
                {posterUrl ? (
                  <img
                    src={posterUrl}
                    alt={itemTitle}
                    className="w-full h-64 object-cover transition-transform duration-300 group-hover:scale-110"
                  />
                ) : (
                  <div className="w-full h-64 bg-netflix-gray flex items-center justify-center">
                    <span className="text-netflix-text">No Image</span>
                  </div>
                )}
                <div className="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-60 transition-all duration-300 flex items-center justify-center">
                  <div className="opacity-0 group-hover:opacity-100 transition-opacity duration-300">
                    <div className="text-white text-center">
                      <Star className="h-6 w-6 mx-auto mb-2 text-imdb-yellow fill-current" />
                      <p className="font-semibold cursor-pointer" onClick={() => onMovieSelect(item)}>View Details</p>
                      <Button 
                        className="mt-2 bg-netflix-red hover:bg-netflix-red/80 text-white px-4 py-2 rounded"
                        onClick={(e) => {
                          e.stopPropagation();
                          const isTV = 'name' in item;
                          const url = isTV 
                            ? `https://pstream.org/media/tmdb-tv-${item.id}/1/1` 
                            : `https://pstream.org/media/tmdb-movie-${item.id}`;
                          window.open(url, '_blank');
                        }}
                      >
                        Stream
                      </Button>
                    </div>
                  </div>
                </div>
              </div>
              
              <div className="mt-2 space-y-1">
                <h4 className="font-semibold truncate text-sm">{itemTitle}</h4>
                <div className="flex items-center space-x-2 text-sm text-netflix-text">
                  <Star className="h-3 w-3 text-imdb-yellow fill-current" />
                  <span>{rating}</span>
                  {itemIsTV && 'number_of_seasons' in item && item.number_of_seasons && (
                    <>
                      <span></span>
                      <span>{item.number_of_seasons} Season{item.number_of_seasons > 1 ? 's' : ''}</span>
                    </>
                  )}
                </div>
              </div>
            </div>
          );
        })}
      </div>
    </section>
  );
}
