import { useState, useEffect } from "react";
import { Dialog, DialogContent } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Star, Play, Film, X, Heart } from "lucide-react";
import { useMovieDetails, useTVShowDetails, useTVSeason, useAddToFavorites } from "@/hooks/use-tmdb";
import { useToast } from "@/hooks/use-toast";
import type { TMDBMovie, TMDBTVShow, TMDBEpisode } from "@/types/tmdb";

interface MovieModalProps {
  content: (TMDBMovie | TMDBTVShow) | null;
  isOpen: boolean;
  onClose: () => void;
  onPlay: (tmdbId: number, isTV: boolean, seasonNumber?: number, episodeId?: number) => void;
}

export default function MovieModal({ content, isOpen, onClose, onPlay }: MovieModalProps) {
  const [selectedSeason, setSelectedSeason] = useState(1);
  const [selectedEpisode, setSelectedEpisode] = useState<TMDBEpisode | null>(null);
  const { toast } = useToast();

  const isTV = content && 'name' in content;
  const contentId = content?.id;

  const { data: movieDetails } = useMovieDetails(contentId || 0, {
    enabled: !!contentId && !isTV,
  });

  const { data: tvDetails } = useTVShowDetails(contentId || 0, {
    enabled: !!contentId && isTV,
  });

  const { data: seasonData } = useTVSeason(contentId || 0, selectedSeason, {
    enabled: !!contentId && isTV,
  });

  const addToFavorites = useAddToFavorites();

  const displayContent = isTV ? tvDetails : movieDetails;

  useEffect(() => {
    if (seasonData?.episodes && seasonData.episodes.length > 0) {
      setSelectedEpisode(seasonData.episodes[0]);
    }
  }, [seasonData]);

  useEffect(() => {
    if (isTV && displayContent && (displayContent as TMDBTVShow).seasons) {
      setSelectedSeason(1);
    }
  }, [displayContent, isTV]);

  if (!content || !displayContent) return null;

  const backdropUrl = displayContent.backdrop_path 
    ? `https://image.tmdb.org/t/p/original${displayContent.backdrop_path}`
    : null;

  const posterUrl = displayContent.poster_path 
    ? `https://image.tmdb.org/t/p/w300${displayContent.poster_path}`
    : null;

  const title = isTV ? (displayContent as TMDBTVShow).name : (displayContent as TMDBMovie).title;
  const rating = displayContent.vote_average?.toFixed(1) || 'N/A';
  const year = isTV 
    ? (displayContent as TMDBTVShow).first_air_date ? new Date((displayContent as TMDBTVShow).first_air_date).getFullYear() : 'N/A'
    : (displayContent as TMDBMovie).release_date ? new Date((displayContent as TMDBMovie).release_date).getFullYear() : 'N/A';

  const runtime = isTV 
    ? `${(displayContent as TMDBTVShow).number_of_seasons || 1} Season${((displayContent as TMDBTVShow).number_of_seasons || 1) > 1 ? 's' : ''}`
    : (displayContent as TMDBMovie).runtime ? `${(displayContent as TMDBMovie).runtime} min` : 'N/A';

  const trailer = displayContent.videos?.find(video => video.type === 'Trailer' && video.site === 'YouTube');

  const handlePlay = () => {
    if (isTV && selectedEpisode) {
      onPlay(displayContent.id, true, selectedSeason, selectedEpisode.id);
    } else {
      onPlay(displayContent.id, isTV);
    }
  };

  const handlePlayTrailer = () => {
    if (trailer) {
      window.open(`https://www.youtube.com/watch?v=${trailer.key}`, '_blank');
    } else {
      toast({
        title: "No trailer available",
        description: "Trailer not found for this content.",
      });
    }
  };

  const handleAddToFavorites = () => {
    addToFavorites.mutate({
      tmdbId: displayContent.id,
      mediaType: isTV ? 'tv' : 'movie',
      title,
      posterPath: displayContent.poster_path
    }, {
      onSuccess: () => {
        toast({
          title: "Added to favorites",
          description: `${title} has been added to your favorites.`,
        });
      },
      onError: () => {
        toast({
          title: "Error",
          description: "Failed to add to favorites.",
          variant: "destructive",
        });
      }
    });
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto bg-netflix-gray border-netflix-light-gray p-0">
        <div className="relative">
          <Button
            variant="ghost"
            size="icon"
            className="absolute top-4 right-4 z-10 bg-black/50 hover:bg-black/70 rounded-full"
            onClick={onClose}
          >
            <X className="h-5 w-5" />
          </Button>

          {/* Backdrop */}
          <div className="relative h-64 md:h-96">
            {backdropUrl ? (
              <img
                src={backdropUrl}
                alt={title}
                className="w-full h-full object-cover rounded-t-lg"
              />
            ) : (
              <div className="w-full h-full bg-netflix-light-gray rounded-t-lg" />
            )}
            <div className="absolute inset-0 bg-gradient-to-t from-netflix-gray to-transparent rounded-t-lg" />
            
            {/* Play button overlay */}
            <div className="absolute inset-0 flex items-center justify-center">
              <Button
                size="lg"
                className="bg-white/20 hover:bg-white/30 rounded-full p-6"
                onClick={handlePlay}
              >
                <Play className="h-8 w-8 text-white ml-1" />
              </Button>
            </div>
          </div>

          {/* Content */}
          <div className="p-6 space-y-6">
            <div className="flex flex-col md:flex-row gap-6">
              <div className="flex-1 space-y-4">
                <h2 className="text-3xl font-bold">{title}</h2>
                
                <div className="flex items-center space-x-4 text-netflix-text">
                  <div className="flex items-center space-x-2">
                    <Star className="h-4 w-4 text-imdb-yellow fill-current" />
                    <span className="text-lg">{rating}</span>
                  </div>
                  <span>{year}</span>
                  <span>{runtime}</span>
                  <span className="bg-netflix-red px-2 py-1 rounded text-sm">{isTV ? 'TV Series' : 'Movie'}</span>
                </div>
                
                <p className="text-netflix-text leading-relaxed">
                  {displayContent.overview || 'No description available.'}
                </p>
                
                <div className="flex space-x-4">
                  <Button className="netflix-button" onClick={handlePlay}>
                    <Play className="mr-2 h-4 w-4" />
                    {isTV ? 'Stream Episode' : 'Stream Now'}
                  </Button>
                  
                  {trailer && (
                    <Button variant="secondary" className="netflix-button-secondary" onClick={handlePlayTrailer}>
                      <Film className="mr-2 h-4 w-4" />
                      Watch Trailer
                    </Button>
                  )}
                  
                  <Button 
                    variant="outline" 
                    className="border-netflix-light-gray hover:bg-netflix-light-gray"
                    onClick={handleAddToFavorites}
                    disabled={addToFavorites.isPending}
                  >
                    <Heart className="mr-2 h-4 w-4" />
                    Add to Favorites
                  </Button>
                </div>
              </div>
              
              {/* Poster */}
              {posterUrl && (
                <div className="w-48 flex-shrink-0">
                  <img
                    src={posterUrl}
                    alt={title}
                    className="w-full rounded-lg"
                  />
                </div>
              )}
            </div>

            {/* TV Show Controls */}
            {isTV && tvDetails && (
              <div className="space-y-4">
                <h3 className="text-xl font-semibold">Episodes</h3>
                
                <div className="flex space-x-4 mb-4">
                  <Select value={selectedSeason.toString()} onValueChange={(value) => setSelectedSeason(parseInt(value))}>
                    <SelectTrigger className="w-48 bg-netflix-light-gray border-netflix-text">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      {Array.from({ length: tvDetails.number_of_seasons || 1 }, (_, i) => (
                        <SelectItem key={i + 1} value={(i + 1).toString()}>
                          Season {i + 1}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
                
                {/* Episode List */}
                {seasonData?.episodes && (
                  <div className="space-y-2 max-h-64 overflow-y-auto">
                    {seasonData.episodes.map((episode) => (
                      <div
                        key={episode.id}
                        className={`flex items-center space-x-4 p-3 rounded-lg cursor-pointer transition-colors ${
                          selectedEpisode?.id === episode.id ? 'bg-netflix-red/20' : 'bg-netflix-light-gray hover:bg-netflix-light-gray/70'
                        }`}
                        onClick={() => setSelectedEpisode(episode)}
                      >
                        <div className="w-20 h-12 bg-netflix-dark rounded flex-shrink-0 flex items-center justify-center">
                          {episode.still_path ? (
                            <img
                              src={`https://image.tmdb.org/t/p/w200${episode.still_path}`}
                              alt={episode.name}
                              className="w-full h-full object-cover rounded"
                            />
                          ) : (
                            <span className="text-xs text-netflix-text">No Image</span>
                          )}
                        </div>
                        
                        <div className="flex-1">
                          <h4 className="font-medium">
                            Episode {episode.episode_number}: {episode.name}
                          </h4>
                          <p className="text-sm text-netflix-text line-clamp-2">
                            {episode.overview || 'No description available.'}
                          </p>
                          <div className="flex items-center space-x-2 text-xs text-netflix-text mt-1">
                            <span>{episode.runtime} min</span>
                            <span></span>
                            <div className="flex items-center space-x-1">
                              <Star className="h-3 w-3 text-imdb-yellow fill-current" />
                              <span>{episode.vote_average?.toFixed(1) || 'N/A'}</span>
                            </div>
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            )}

            {/* Cast */}
            {displayContent.credits && displayContent.credits.length > 0 && (
              <div className="space-y-4">
                <h3 className="text-xl font-semibold">Cast</h3>
                <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                  {displayContent.credits.slice(0, 8).map((actor) => (
                    <div key={actor.id} className="text-center">
                      <div className="w-16 h-16 rounded-full mx-auto mb-2 overflow-hidden bg-netflix-light-gray">
                        {actor.profile_path ? (
                          <img
                            src={`https://image.tmdb.org/t/p/w200${actor.profile_path}`}
                            alt={actor.name}
                            className="w-full h-full object-cover"
                          />
                        ) : (
                          <div className="w-full h-full flex items-center justify-center text-netflix-text text-xs">
                            No Photo
                          </div>
                        )}
                      </div>
                      <p className="font-medium text-sm">{actor.name}</p>
                      <p className="text-xs text-netflix-text">{actor.character}</p>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
}
