import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Star, Play } from "lucide-react";
import { useTopRatedMovies, useNowPlayingMovies, usePopularTVShows } from "@/hooks/use-tmdb";
import type { TMDBMovie, TMDBTVShow } from "@/types/tmdb";

interface RandomPickerProps {
  onContentSelect: (content: TMDBMovie | TMDBTVShow) => void;
}

export default function RandomPicker({ onContentSelect }: RandomPickerProps) {
  const [randomContent, setRandomContent] = useState<(TMDBMovie | TMDBTVShow) | null>(null);
  const [isTV, setIsTV] = useState(false);
  const [activeButton, setActiveButton] = useState<'top50' | 'latest' | 'tv' | null>(null);
  
  const { data: topRatedMovies } = useTopRatedMovies();
  const { data: nowPlayingMovies } = useNowPlayingMovies();
  const { data: popularTVShows } = usePopularTVShows();

  const getRandomContent = (type: 'top50' | 'latest' | 'tv') => {
    let data;
    let isTVShow = false;

    switch (type) {
      case 'top50':
        data = topRatedMovies?.results;
        break;
      case 'latest':
        data = nowPlayingMovies?.results;
        break;
      case 'tv':
        data = popularTVShows?.results;
        isTVShow = true;
        break;
    }

    if (data && data.length > 0) {
      const randomIndex = Math.floor(Math.random() * Math.min(data.length, 20));
      const selected = data[randomIndex];
      setRandomContent(selected);
      setIsTV(isTVShow);
      setActiveButton(type);
    }
  };

  const posterUrl = randomContent?.poster_path 
    ? `https://image.tmdb.org/t/p/w300${randomContent.poster_path}`
    : null;

  const title = isTV ? (randomContent as TMDBTVShow)?.name : (randomContent as TMDBMovie)?.title;
  const rating = randomContent?.vote_average?.toFixed(1) || 'N/A';
  const year = isTV 
    ? (randomContent as TMDBTVShow)?.first_air_date ? new Date((randomContent as TMDBTVShow).first_air_date).getFullYear() : 'N/A'
    : (randomContent as TMDBMovie)?.release_date ? new Date((randomContent as TMDBMovie).release_date).getFullYear() : 'N/A';

  return (
    <section className="space-y-6">
      <div className="flex flex-col md:flex-row items-start md:items-center justify-between mb-6">
        <h3 className="text-2xl font-bold mb-4 md:mb-0">Pick a Random Movie</h3>
        <div className="flex flex-wrap gap-4">
          <Button 
            className={activeButton === 'top50' ? "netflix-button" : "netflix-button-secondary"}
            onClick={() => getRandomContent('top50')}
          >
            Top 50 Movies
          </Button>
          <Button 
            className={activeButton === 'latest' ? "netflix-button" : "netflix-button-secondary"}
            onClick={() => getRandomContent('latest')}
          >
            Latest Movies
          </Button>
          <Button 
            className={activeButton === 'tv' ? "netflix-button" : "netflix-button-secondary"}
            onClick={() => getRandomContent('tv')}
          >
            Random TV Show
          </Button>
        </div>
      </div>
      
      {randomContent && (
        <Card className="bg-netflix-gray border-netflix-light-gray">
          <CardContent className="p-6">
            <div className="flex flex-col md:flex-row gap-6">
              {posterUrl && (
                <img 
                  src={posterUrl}
                  alt={title}
                  className="w-32 h-48 object-cover rounded-lg flex-shrink-0 mx-auto md:mx-0"
                />
              )}
              
              <div className="flex-1 space-y-4">
                <h4 className="text-2xl font-bold">{title}</h4>
                
                <div className="flex items-center space-x-4 text-netflix-text">
                  <div className="flex items-center space-x-2">
                    <Star className="h-4 w-4 text-imdb-yellow fill-current" />
                    <span>{rating}</span>
                  </div>
                  <span>{year}</span>
                  <span>{isTV ? 'TV Show' : 'Movie'}</span>
                </div>
                
                <p className="text-netflix-text leading-relaxed">
                  {randomContent.overview || 'No description available.'}
                </p>
                
                <div className="flex space-x-4">
                  <Button 
                    className="netflix-button"
                    onClick={() => onContentSelect(randomContent)}
                  >
                    <Play className="mr-2 h-4 w-4" />
                    Watch Now
                  </Button>
                  <Button 
                    variant="outline"
                    className="border-netflix-light-gray hover:bg-netflix-light-gray"
                    onClick={() => onContentSelect(randomContent)}
                  >
                    View Details
                  </Button>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      )}
    </section>
  );
}
